<template>
  <div class="projects-main">

    <transition name="component-opacity" >
    <div class="main-width-image" v-if="screenWidth && currentImageHover!==''" key="main-image1">
        <img class="project-image" :src="require('../assets/images/'+ currentImageHover )" alt="" id="general-image">
    </div>
    </transition>

    <div class="projects-list" >
      <ul class="projects-ul">

        <li class="project" v-for="(project, index) in projects" :key="index">
          <a :href="project.url" target="_blank" class="project-link opacity-low" @mouseover="updateMainImage(project)" @mouseout="currentImageHover = ''" :class="{'opacity-one': project.imgUrl===currentImageHover}">{{project.name}}</a>
          <div class="image-container">
            <img class="project-image" :src="require('../assets/images/' + project.imgUrl)" alt="" v-if="!screenWidth">
          </div>
        </li>

      </ul>
    </div>
    
  </div>
</template>

<script>
import { setTimeout } from 'timers';

export default {
  name: 'Projects',
  props: {
  },
  data() {
    return {
      projects: [{name: 'Mad Doctor',imgUrl: 'gloomy.jpg', url: "https://vacuousrom.github.io/maddoctor/"},],
      windowInnerWidth: window.innerWidth,
      currentImageHover: '',
    
    }
  },
  methods: {

    getWindowWidth() {
      this.windowInnerWidth = window.innerWidth
    },

    updateMainImage(project){
      let self = this;
      if (this.currentImageHover === project.imgUrl) {
        return;
      } else {
        this.currentImageHover='';
        setTimeout(function() {
          // this.$el.querySelector('.main-width-image').classList.add('scale-up')
        self.currentImageHover=project.imgUrl;
          
          // this.$el.querySelector('.main-width-image').classList.remove('scale-up')
        }, 200)
      }
    }

  },
  computed: {
    screenWidth() {
      if (this.windowInnerWidth >=1000) {
        return true;
      } else {
        return false;
      }
    },
    
  },
  created(){
    window.addEventListener('resize', this.getWindowWidth)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .projects-main {
    width: 100%;
    height: 100vh;
    background: #000;
    display: grid;
    color: #fff;
    justify-items: center;
    align-items: center;
  }

  .project {
    
    position: relative;
  }

  .project-image {
    transition: all 350ms cubic-bezier(0.77, 0, 0.175, 1);
    -webkit-transition: all 350ms cubic-bezier(0.77, 0, 0.175, 1);
    -moz-transition: all 350ms cubic-bezier(0.77, 0, 0.175, 1);
    -o-transition: all 350ms cubic-bezier(0.77, 0, 0.175, 1);
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
  }

  

  /* media for phones */
@media (max-width: 999px) {
  .project-link {
    z-index: 3;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 3rem;
    text-align: center;
    font-weight: 600;
  }

  .image-container {
    height: 100vh;
  }
}

  /* media for screens */

@media (min-width: 1000px) {
  .projects-ul {
    display: grid;
    justify-items: center;
    align-items: center;
    /* grid-template-rows: repeat(4, 1fr); */
    height: 100vh;
    width: 100%;
  }

  .project {
    font-size: 5rem;
    font-weight: 700;
  }

  .main-width-image {
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    background-color: #000;

    transition: all 350ms cubic-bezier(0.77, 0, 0.175, 1);
    -webkit-transition: all 350ms cubic-bezier(0.77, 0, 0.175, 1);
    -moz-transition: all 350ms cubic-bezier(0.77, 0, 0.175, 1);
    -o-transition: all 350ms cubic-bezier(0.77, 0, 0.175, 1);
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
  }

  .opacity-low {
    transition: .3s all ease-in-out;
    opacity: .2;
  }

  .opacity-one {
    opacity: 1;
  }
}
/* transition for the image */

.component-opacity-enter {
    transform: translateX(-100%);
    opacity: 0;
}
.component-opacity-enter-to {
    opacity: 1;
}
.component-opacity-leave-to {
    transform: translateX(-100%);
  
    opacity: 0;
}


</style>
